















  export default{
    props:{
      maskShow:{
        type:Boolean,
        default: false
      },
      popupShow:{
        type:Boolean,
        default: false
      },
      shareType:{
        type:Number,
        default:0,
      },
      shareData:{
        type:Object
      }
    },
    data(){
      return{
//         shareText:'',
//         href:"",
//         image: '',
        providerList:[],
      }
    },
    onShareAppMessage() {
    	return {
    		title: this.shareData.title,
    		path: this.shareData.path,
    		imageUrl:this.shareData.imageUrl
    	}
    },
    onUnload:function(){
    	this.shareText=this.data.shareText,
    	this.href = this.data.href,
    	this.image=this.data.image;
    },
    onLoad() {
    	uni.getProvider({
    		service: "share",
    		success: (e) => {
    			console.log("success", e);
    			let data = []
    			for (let i = 0; i < e.provider.length; i++) {
    				switch (e.provider[i]) {
    					case 'weixin':
    						data.push({
    							name: '分享到微信',
    							id: 'weixin',
                  type:'WXSceneSession',
    							sort:0,
                  imgUrl:"../../static/shareIconhaoyou.png",
    						})
    						data.push({
    							name: '朋友圈',
    							id: 'weixin',
    							type:'WXSenceTimeline',
    							sort:1,
                  imgUrl:"../../static/shareIconPeng.png",
    						})
    						break;
    					case 'qq':
    						data.push({
    							name: 'QQ好友',
    							id: 'qq',
    							sort:3,
                  imgUrl:"../../static/shareQQ.png",
    						})
    						break;
    					default:
    						break;
    				}
    			}
    			this.providerList = data.sort((x,y) => {
    				return x.sort - y.sort
    			});
    		},
    		fail: (e) => {
    			console.log("获取登录通道失败", e);
    			uni.showModal({
    				content:"获取登录通道失败",
    				showCancel:false
    			})
    		}
    	});
    },
    methods:{
      hide(){
        this.maskShow = false
        this.popupShow = false
      },
     async share(e) {
     	console.log("分享通道:"+ e.id +"； 分享类型:" + this.shareType);
      if(e.id==='qq'){
        this.shareType=1
      }else{
        this.shareType=0
      }	
      console.log("qq分享类型:" + this.shareType);
     	let shareOPtions = {
     		provider: e.id,
     		scene: e.type && e.type === 'WXSenceTimeline' ? 'WXSenceTimeline' : "WXSceneSession", //WXSceneSession”分享到聊天界面，“WXSenceTimeline”分享到朋友圈，“WXSceneFavorite”分享到微信收藏     
     		type:this.shareType,
        href:this.shareData.href,
        summary:this.shareData.shareText,
        imageUrl:this.shareData.image,
        title:this.shareData.title,
     		success: (e) => {
     			console.log("success", e);
     		},
     		fail: (e) => {
     			console.log("fail", e)
     		},
     		complete:function(){
     			console.log("分享操作结束!")
     		}
     	}
     	if(shareOPtions.type === 0 && shareOPtions.provider === 'qq'){//如果是分享文字到qq，则必须加上href和title
        console.log('分享到扣扣')
     		shareOPtions.href = "baidu.com";
     		shareOPtions.title = "欢迎体验健康联盟";
     	}
      console.log(shareOPtions,'当前shareOPtions')
     	uni.share(shareOPtions);
     },
    }
  }



